import React, { useContext } from 'react'
import { signInWithGoogle } from '../../util/Firebase'
import { Button, Grid, Typography } from '@material-ui/core'
import { AuthContext } from '../../util/Auth';
import { Redirect } from 'react-router';


const Signin = () => {
    const { currentUser } = useContext(AuthContext);
    if (currentUser) {
        return <Redirect to={'/'} />;
    }
    return (
        <Grid container direction="column" style={{ padding: 20 }} justify="center">
            <Typography align="center" variant="h4">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</Typography>
            <Typography variant="body1" align="center">–ù–∞–∂–º–∏—Ç–µ –Ω–∞ –∫–Ω–æ–ø–∫—É (–í—Ö–æ–¥ –∏–ª–∏ –†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è).</Typography>
            <Typography variant="body1" align="center">–ï—Å–ª–∏ —É –≤–∞—Å –Ω–µ—Ç –∞–∫–∫–∞—É–Ω—Ç–∞ Google, —Ç–æ —Å–æ–∑–¥–∞–π—Ç–µ –µ–≥–æ.</Typography>
            <br />
            <Button size="large" color="primary" variant="contained" onClick={signInWithGoogle}>–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</Button>
        </Grid>
    )
}

export default Signin