import {Button, Grid, Typography} from '@material-ui/core';
import React, {useState, useEffect} from 'react'
import {useParams} from 'react-router';
import firebase from '../../util/Firebase'
import {jsPDF} from "jspdf";


const Badge = (props) => {
    const {prefix, id} = useParams();
    const [badges, setBadges] = useState([])

    const fetchImage = async (blob) => {
        let dataUrl = await new Promise(resolve => {
            let reader = new FileReader();
            reader.onload = () => resolve(reader.result);
            reader.readAsDataURL(blob);
        });
        return dataUrl
    }

    const downloadUrlAsPromise = (url) => {
        return new Promise((resolve, reject) => {
            var xhr = new XMLHttpRequest();
            xhr.open('GET', url);
            xhr.responseType = "blob";
            xhr.onreadystatechange = (evt) => {
                if (xhr.readyState === 4) {
                    if (xhr.status === 200) {
                        resolve(xhr.response);
                    } else {
                        reject(new Error("Ajax error for " + url + ": " + xhr.status));
                    }
                }
            }
            xhr.send();
        })
    }

    const getPdfFromImage = async (url, filename) => {
        let blob = await downloadUrlAsPromise(url)
        let dataUrl = await fetchImage(blob)
        const pdfDoc = new jsPDF();
        pdfDoc.addImage(dataUrl, "JPEG", 0, 0, 70, 105);
        pdfDoc.save(filename)
    }

    // console.log('id', typeof id, 'type', prefix)
    useEffect(() => {
        if (id && prefix) {
            firebase.firestore().collection('badges').where('prefix', '==', prefix).where('number', '==', parseInt(id)).get().then(snap => {
                let tmp = []
                snap.forEach(doc => {
                    if (doc.data().file_url) {
                        tmp.push(
                            // <Grid container item justify="center" key={doc.id}><img style={{ maxWidth: '100%' }} src={doc.data().file_url} alt={doc.data().file_url} /></Grid>
                            <Grid container item justify="center" style={{paddingTop: 10}}>
                                <Grid container item justify="center">
                                    <Button variant="contained" color="primary"
                                            onClick={() => getPdfFromImage(doc.data().file_url, doc.id)}>–°–∫–∞—á–∞—Ç—å
                                        PDF</Button>
                                </Grid>
                                <Grid container item justify="center" key={doc.id}>
                                    <img style={{maxWidth: '100%'}} src={doc.data().file_url}
                                         alt={doc.data().file_url}/>
                                </Grid>
                            </Grid>
                        )
                    }
                })
                return tmp
            }).then(tmp => {
                console.log("TMP", tmp)
                setBadges(tmp)
            })
        }
    }, [id, prefix])

    return (
        badges.length > 0 ?
            <Grid container direction="column" justify="center" spacing={1}>
                {/*<Grid item>*/}
                {/*    <Typography align="center" variant="h6">–£–¥–æ—Å—Ç–æ–≤–µ—Ä–µ–Ω–∏–µ –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ –¥–æ 01.12.2021–≥. –ú—ã –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–∞–µ–º,*/}
                {/*        —á—Ç–æ —ç—Ç–æ—Ç —á–µ–ª–æ–≤–µ–∫ —è–≤–ª—è–µ—Ç—Å—è –≤–Ω–µ—à—Ç–∞—Ç–Ω—ã–º –∂—É—Ä–Ω–∞–ª–∏—Å—Ç–æ–º ¬´–ö–ª–æ–æ–ø –ú–µ–¥–∏–∞¬ª.</Typography>*/}
                {/*</Grid>*/}
                {/*<Grid item>*/}
                {/*    <Typography align="center" variant="h6">–ö“Ø–±”©–ª“Ø–∫ 01.12.2021 —á–µ–π–∏–Ω –∂–∞—Ä–∞–∫—Ç—É—É. –ë–∏–∑ –±—É–ª –∞–¥–∞–º ¬´–ö–ª–æ–æ–ø*/}
                {/*        –ú–µ–¥–∏–∞–Ω—ã–Ω¬ª —à—Ç–∞—Ç—Ç–∞–Ω —Ç—ã—à–∫–∞—Ä–∫—ã –∂—É—Ä–Ω–∞–ª–∏—Å—Ç–∏ —ç–∫–µ–Ω–∏–Ω —Ç–∞—Å—Ç—ã–∫—Ç–∞–π–±—ã–∑.</Typography>*/}
                {/*</Grid>*/}
                {badges[0]}
            </Grid>
            :
            <Grid container direction="column" justify="center" spacing={1}>
                <Grid item>
                    <Typography align="center" variant="h5">–ï—Å–ª–∏ —Å—Å—ã–ª–∫–∞ –Ω–µ –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç—Å—è ‚Äî –∑–Ω–∞—á–∏—Ç, —É –Ω–∞—Å –Ω–µ—Ç —Ç–∞–∫–æ–≥–æ
                        –≤–Ω–µ—à—Ç–∞—Ç–Ω–æ–≥–æ –∂—É—Ä–Ω–∞–ª–∏—Å—Ç–∞, —ç—Ç–æ –º–æ–≥—É—Ç –±—ã—Ç—å –º–æ—à–µ–Ω–Ω–∏–∫–∏.</Typography>
                </Grid>
                <Grid item>
                    <Typography align="center" variant="h5">–≠–≥–µ—Ä–¥–µ —à–∏–ª—Ç–µ–º–µ –∞—á—ã–ª–±–∞–π –∂–∞—Ç—Å–∞, –¥–µ–º–µ–∫ –±–∏–∑–¥–∏–Ω –º—ã–Ω–¥–∞–π —à—Ç–∞—Ç—Ç–∞–Ω
                        —Ç—ã—à–∫–∞—Ä–∫—ã –∂—É—Ä–Ω–∞–ª–∏—Å—Ç–∏–±–∏–∑ –∂–æ–∫. –ë—É–ª –∞—Ñ–µ—Ä–∏—Å—Ç—Ç–µ—Ä –±–æ–ª—É—à—É –º“Ø–º–∫“Ø–Ω.</Typography>
                </Grid>
            </Grid>
    )
}

export default Badge