import { Button, Grid, Typography } from '@material-ui/core';
import React, { useState, useEffect, useContext } from 'react'
import firebase from '../../util/Firebase'
import { AuthContext } from "../../util/Auth";
import { jsPDF } from "jspdf";


const UserBadges = (props) => {
    const { currentUser } = useContext(AuthContext);
    const [badges, setBadges] = useState([])

    const fetchImage = async (blob) => {
        let dataUrl = await new Promise(resolve => {
            let reader = new FileReader();
            reader.onload = () => resolve(reader.result);
            reader.readAsDataURL(blob);
        });
        return dataUrl
    }

    const downloadUrlAsPromise = (url) => {
        return new Promise((resolve, reject) => {
            var xhr = new XMLHttpRequest();
            xhr.open('GET', url);
            xhr.responseType = "blob";
            xhr.onreadystatechange = (evt) => {
                if (xhr.readyState === 4) {
                    if (xhr.status === 200) {
                        resolve(xhr.response);
                    } else {
                        reject(new Error("Ajax error for " + url + ": " + xhr.status));
                    }
                }
            }
            xhr.send();
        })
    }

    const getPdfFromImage = async (url, filename) => {
        let blob = await downloadUrlAsPromise(url)
        let dataUrl = await fetchImage(blob)
        const pdfDoc = new jsPDF();
        pdfDoc.addImage(dataUrl, "JPEG", 0, 0, 70, 105);
        pdfDoc.save(filename)
    }

    useEffect(() => {
        if (currentUser) {
            firebase.firestore().collection('badges').where('user_id', '==', currentUser.uid).get().then(snap => {
                let tmp = []
                snap.forEach(doc => {
                    if (doc.data().file_url) {
                        tmp.push(
                            <Grid container item justify="center" style={{ paddingTop: 10 }}>
                                <Grid container item justify="center">
                                    <Button variant="contained" color="primary" onClick={() => getPdfFromImage(doc.data().file_url, doc.id)}>–°–∫–∞—á–∞—Ç—å PDF</Button>
                                </Grid>
                                <Grid container item justify="center" key={doc.id}>
                                    <img style={{ maxWidth: '100%' }} src={doc.data().file_url} alt={doc.data().file_url} />
                                </Grid>
                            </Grid>
                        )
                    }
                })
                return tmp
            }).then(tmp => {
                console.log("TMP", tmp)
                setBadges(tmp)
            })
        }
    }, [currentUser])

    return (
        badges.length > 0 ?
            <Grid container direction="column" justify="center" spacing={1}>
                {/*<Grid item>*/}
                {/*    <Typography align="center" variant="h6">–£–¥–æ—Å—Ç–æ–≤–µ—Ä–µ–Ω–∏–µ –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ –¥–æ 01.12.2021–≥. –ú—ã –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–∞–µ–º, —á—Ç–æ —ç—Ç–æ—Ç —á–µ–ª–æ–≤–µ–∫ —è–≤–ª—è–µ—Ç—Å—è –≤–Ω–µ—à—Ç–∞—Ç–Ω—ã–º –∂—É—Ä–Ω–∞–ª–∏—Å—Ç–æ–º ¬´–ö–ª–æ–æ–ø –ú–µ–¥–∏–∞¬ª.</Typography>*/}
                {/*</Grid>*/}
                {/*<Grid item>*/}
                {/*    <Typography align="center" variant="h6">–ö“Ø–±”©–ª“Ø–∫ 01.12.2021 —á–µ–π–∏–Ω –∂–∞—Ä–∞–∫—Ç—É—É. –ë–∏–∑ –±—É–ª –∞–¥–∞–º ¬´–ö–ª–æ–æ–ø –ú–µ–¥–∏–∞–Ω—ã–Ω¬ª —à—Ç–∞—Ç—Ç–∞–Ω —Ç—ã—à–∫–∞—Ä–∫—ã –∂—É—Ä–Ω–∞–ª–∏—Å—Ç–∏ —ç–∫–µ–Ω–∏–Ω —Ç–∞—Å—Ç—ã–∫—Ç–∞–π–±—ã–∑.</Typography>*/}
                {/*</Grid>*/}
                {badges}
            </Grid>
            :
            <Grid container direction="column" justify="center" spacing={1}>
                <Grid item>
                    <Typography align="center" variant="h6">–£ –≤–∞—Å –Ω–µ—Ç –∞–∫—Ç–∏–≤–Ω—ã—Ö —É–¥–æ—Å—Ç–æ–≤–µ—Ä–µ–Ω–∏–π.</Typography>
                </Grid>
                <Grid item>
                    <Typography align="center" variant="h6">–°–∏–∑–¥–∏–Ω –∞–∫—Ç–∏–≤–¥“Ø“Ø –∫“Ø–±”©–ª“Ø–≥“Ø“£“Ø–∑ –∂–æ–∫.</Typography>
                </Grid>
            </Grid>
    )
}

export default UserBadges